//<ÐžÐ‘ÐÐ£Ð›Ð•ÐÐ˜Ð•, ÐžÐ‘Ð©Ð˜Ð• ÐŸÐÐ ÐÐœÐ•Ð¢Ð Ð«>===============================================================================================
:root {
    --clr-default-100: #fff;
    --clr-default-200: #d4e1ef;
    --clr-default-700: #445578;
    --clr-default-800: #1b273f;
    --clr-default-900: #000;
    --clr-primary-300: #0099ff;
    --clr-primary-400: #1c6ae3;
    --clr-primary-500: #0e57ca;
}

@import "./settings/normalize";

body {
    font-family: $font-family;
    font-weight: 500;
    font-size: 1.6rem;
    color: var(--clr-default-800);

    &.lock {
        overflow: hidden;

        &--wrapper {
            .wrapper::before {
                opacity: 1;
                visibility: visible;
                transition: opacity $ms, visibility $ms;
            }
        }
    }
}

//</ÐžÐ‘ÐÐ£Ð›Ð•ÐÐ˜Ð•, ÐžÐ‘Ð©Ð˜Ð• ÐŸÐÐ ÐÐœÐ•Ð¢Ð Ð«>===============================================================================================

//<ÐžÐ‘ÐžÐ›ÐžÐ§ÐšÐ>===========================================================================================================
.wrapper {
    overflow: hidden;
    width: 100%;
    min-width: 320px;
    min-height: 100%;
    display: flex;
    flex-direction: column;

    &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 5;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        visibility: hidden;
        transition: opacity $ms, visibility $ms;
    }
}

//</ÐžÐ‘ÐžÐ›ÐžÐ§ÐšÐ>===========================================================================================================

//<ÐžÐ¡ÐÐžÐ’ÐÐÐ¯ Ð¡Ð•Ð¢ÐšÐ>===========================================================================================================
.container {
